import React, { useState, useEffect, useRef } from "react";
import Papa from "papaparse";
import DataTable from "react-data-table-component";
import { CSVLink } from "react-csv";
import * as XLSX from "xlsx";
import "tailwindcss/tailwind.css";
import * as d3 from "d3";

import html2canvas from "html2canvas";

const App = () => {
  const [csvData, setCsvData] = useState([]);
  const [filteredData, setFilteredData] = useState([]);
  const [selectedRows, setSelectedRows] = useState([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [xColumn, setXColumn] = useState("");
  const [yColumn, setYColumn] = useState("");
  const [chartType, setChartType] = useState("bar");

  const [svgWidth, setSvgWidth] = useState(800);
  const [svgHeight, setSvgHeight] = useState(400);

  const chartRef = useRef(null);

  const wrapperRef = useRef();

  useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search);
    if (urlParams.has("c") && urlParams.get("c") === "csv") {
      readClipboard("csv");
    } else if (urlParams.has("c") && urlParams.get("c") === "json") {
      readClipboard("json");
    }
  }, []);

  useEffect(() => {
    if (xColumn && yColumn && chartType) {
      drawChart();
    }
  }, [xColumn, yColumn, chartType, filteredData, selectedRows]); // Redraw when selectedRows changes

  useEffect(() => {
    setFilteredData(csvData); // Reset filteredData when csvData changes
  }, [csvData]);

  const downloadChart = () => {
    html2canvas(wrapperRef.current)
      .then((canvas) => {
        const link = document.createElement("a");
        link.href = canvas.toDataURL("image/png");
        link.download = "chart.png";
        link.click();
      })
      .catch((err) => console.error("Error capturing chart:", err));
  };

  const readClipboard = async (type) => {
    try {
      const text = await navigator.clipboard.readText();
      if (type === "csv") {
        parseCsvData(text);
      } else if (type === "json") {
        parseJsonData(text);
      }
    } catch (err) {
      console.error("Failed to read clipboard contents: ", err);
    }
  };

  const handleFileUpload = (e) => {
    const file = e.target.files[0];
    Papa.parse(file, {
      header: true,
      skipEmptyLines: true,
      complete: (result) => {
        setCsvData(result.data);
      },
    });
  };

  const parseCsvData = (data) => {
    const parsedData = Papa.parse(data, { header: true }).data;
    setCsvData(parsedData);
  };

  const parseJsonData = (data) => {
    try {
      const jsonData = JSON.parse(data);
      setCsvData(jsonData);
    } catch (err) {
      console.error("Failed to parse JSON: ", err);
    }
  };

  const handleRowSelected = (state) => {
    setSelectedRows(state.selectedRows);
  };

  const handleSearch = (e) => {
    const value = e.target.value;
    setSearchTerm(value);
    const filtered = csvData.filter((row) =>
      Object.values(row).some((cell) =>
        String(cell).toLowerCase().includes(value.toLowerCase())
      )
    );
    setFilteredData(filtered);
  };

  const columns = Object.keys(csvData[0] || {}).map((key) => ({
    name: key,
    selector: (row) => row[key],
    sortable: true,
  }));

  const exportToXLS = (data, filename) => {
    const worksheet = XLSX.utils.json_to_sheet(data);
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, "Sheet1");
    XLSX.writeFile(workbook, filename);
  };

  const drawChart = () => {
    const svg = d3.select(chartRef.current);
    svg.selectAll("*").remove();

    const width = +svg.attr("width");
    const height = +svg.attr("height");
    const margin = { top: 20, right: 30, bottom: 40, left: 50 };
    const chartWidth = width - margin.left - margin.right;
    const chartHeight = height - margin.top - margin.bottom;

    const g = svg
      .append("g")
      .attr("transform", `translate(${margin.left},${margin.top})`);

    let dataToUse = filteredData; // Use all data by default

    if (selectedRows.length > 0) {
      // Use selected rows if any are selected
      dataToUse = selectedRows;
    }

    const xValues = dataToUse.map((d) => d[xColumn]);
    const yValues = dataToUse.map((d) => d[yColumn]);

    const xScale = d3
      .scaleBand()
      .domain(xValues)
      .range([0, chartWidth])
      .padding(0.1);

    const yScale = d3
      .scaleLinear()
      .domain([0, d3.max(yValues)])
      .range([chartHeight, 0]);

    const color = d3.scaleOrdinal(d3.schemeCategory10);

    const xAxis = d3.axisBottom(xScale);
    const yAxis = d3.axisLeft(yScale);

    g.append("g")
      .attr("class", "x-axis")
      .attr("transform", `translate(0,${chartHeight})`)
      .call(xAxis)
      .selectAll("text")
      .attr("transform", "rotate(-45)")
      .style("text-anchor", "end")
      .attr("dx", "-.8em")
      .attr("dy", ".15em");

    g.append("g").attr("class", "y-axis").call(yAxis);

    if (chartType === "bar") {
      g.selectAll(".bar")
        .data(dataToUse)
        .enter()
        .append("rect")
        .attr("class", "bar")
        .attr("x", (d) => xScale(d[xColumn]))
        .attr("y", (d) => yScale(d[yColumn]))
        .attr("width", xScale.bandwidth())
        .attr("height", (d) => chartHeight - yScale(d[yColumn]))
        .attr("fill", (d, i) => color(i));
    } else if (chartType === "line") {
      const line = d3
        .line()
        .x((d) => xScale(d[xColumn]) + xScale.bandwidth() / 2)
        .y((d) => yScale(d[yColumn]));

      g.append("path")
        .datum(dataToUse)
        .attr("class", "line")
        .attr("d", line)
        .attr("fill", "none")
        .attr("stroke", "steelblue")
        .attr("stroke-width", 2);
    } else if (chartType === "pie") {
      const radius = Math.min(chartWidth, chartHeight) / 2;
      const pie = d3.pie().value((d) => d[yColumn]);
      const arc = d3
        .arc()
        .outerRadius(radius - 10)
        .innerRadius(0);

      const pieGroup = g
        .append("g")
        .attr("transform", `translate(${chartWidth / 2},${chartHeight / 2})`);

      const path = pieGroup
        .selectAll("path")
        .data(pie(dataToUse))
        .enter()
        .append("path")
        .attr("d", arc)
        .attr("fill", (d, i) => color(i));

      // Pie chart labels
      const label = d3
        .arc()
        .outerRadius(radius - 40)
        .innerRadius(radius - 40);

      pieGroup
        .selectAll("text")
        .data(pie(dataToUse))
        .enter()
        .append("text")
        .attr("transform", (d) => `translate(${label.centroid(d)})`)
        .attr("dy", "0.35em")
        .text((d) => d.data[xColumn])
        .style("text-anchor", "middle")
        .style("font-size", "12px");
    }
  };

  // Handle SVG width change
  const handleSvgWidthChange = (e) => {
    setSvgWidth(Number(e.target.value));
  };

  // Handle SVG height change
  const handleSvgHeightChange = (e) => {
    setSvgHeight(Number(e.target.value));
  };

  return (
    <div
      className="App"
      style={{
        border: "2px solid #99ccff",
        boxShadow: "0 4px 8px rgba(0, 0, 0, 0.1)",
      }}
    >
      <header className="bg-blue-500 text-white text-center p-4 mb-4 rounded">
        <h1 className="text-2xl">CSV Editor App</h1>
      </header>
      <nav className="flex justify-between items-center py-4">
        <div>
          <label className="block">
            <span className="sr-only">Upload CSV</span>
            <input
              type="file"
              accept=".csv"
              onChange={handleFileUpload}
              className="block w-full text-sm text-gray-900 border border-gray-300 rounded-lg cursor-pointer bg-gray-50"
            />
          </label>
        </div>
      </nav>
      {csvData.length > 0 && (
        <div className="flex justify-between items-center mb-4">
          <input
            type="text"
            value={searchTerm}
            onChange={handleSearch}
            placeholder="Search..."
            className="p-2 border rounded-md"
          />
        </div>
      )}

      <DataTable
        title="CSV Data"
        columns={columns}
        data={filteredData}
        selectableRows
        onSelectedRowsChange={handleRowSelected}
        pagination
        highlightOnHover
        striped
        className="mt-4"
      />

      {csvData.length > 0 && (
        <div className="mt-4">
          <CSVLink
            data={csvData}
            filename={"edited-data.csv"}
            className="bg-blue-500 text-white py-2 px-4 rounded"
          >
            Save CSV
          </CSVLink>
        </div>
      )}

      {csvData.length > 0 && (
        <div className=" container mx-auto p-4  bg-blue-100">
          <div className="flex justify-between">
            <div>
              <label className="mr-2">X-Axis:</label>
              <select
                value={xColumn}
                onChange={(e) => setXColumn(e.target.value)}
                className="mr-4 p-2 border rounded-md"
              >
                <option value="">Select Column</option>
                {Object.keys(csvData[0] || {}).map((col) => (
                  <option key={col} value={col}>
                    {col}
                  </option>
                ))}
              </select>
              <label className="mr-2">Y-Axis:</label>
              <select
                value={yColumn}
                onChange={(e) => setYColumn(e.target.value)}
                className="mr-4 p-2 border rounded-md"
              >
                <option value="">Select Column</option>
                {Object.keys(csvData[0] || {}).map((col) => (
                  <option key={col} value={col}>
                    {col}
                  </option>
                ))}
              </select>
              <label className="mr-2">Chart Type:</label>
              <select
                value={chartType}
                onChange={(e) => setChartType(e.target.value)}
                className="mr-4 p-2 border rounded-md"
              >
                <option value="bar">Bar</option>
                <option value="line">Line</option>
                <option value="pie">Pie</option>
              </select>
            </div>
            <div>
              {selectedRows.length > 0 && (
                <>
                  <CSVLink
                    data={selectedRows}
                    filename={"selected-rows.csv"}
                    className="bg-green-500 text-white py-2 px-4 rounded"
                  >
                    Save Selected Rows
                  </CSVLink>
                  <button
                    onClick={() =>
                      exportToXLS(selectedRows, "selected-rows.xlsx")
                    }
                    className="bg-orange-500 text-white py-2 px-4 rounded"
                  >
                    Export Selected to XLS
                  </button>
                </>
              )}
              <button
                onClick={() => exportToXLS(csvData, "edited-data.xlsx")}
                className="bg-yellow-500 text-white py-2 px-4 rounded"
              >
                Export to XLS
              </button>
            </div>
          </div>
          <div className="mt-4">
            <label htmlFor="svgWidth" className="mr-2">
              SVG Width:
            </label>
            <input
              type="number"
              id="svgWidth"
              value={svgWidth}
              onChange={handleSvgWidthChange}
              className="p-2 border rounded-md"
            />
            <label htmlFor="svgHeight" className="ml-4 mr-2">
              SVG Height:
            </label>
            <input
              type="number"
              id="svgHeight"
              value={svgHeight}
              onChange={handleSvgHeightChange}
              className="p-2 border rounded-md"
            />

            <button
              onClick={downloadChart}
              className="bg-purple-500 text-white py-2 px-4 rounded"
            >
              Save as PNG
            </button>
          </div>
        </div>
      )}

      <div className="mt-4">
      <div ref={wrapperRef}>
        <svg ref={chartRef} width={svgWidth} height={svgHeight}></svg>
      </div>
      </div>
    </div>
  );
};

export default App;
